import React, { useState, useCallback, useMemo, ChangeEvent } from 'react';
import Layout from 'components/Layout/Base';
import SEO from 'src/components/SEO';
import { TextField, Grid, InputAdornment, makeStyles, Theme } from '@material-ui/core';
import * as Icon from 'react-feather';
import { useAllPosts } from 'src/hooks/useAllPosts';
import PostList from 'src/components/Lists/PostList';
import { linkEvent, searchEvent } from 'src/utils/loggers';
import { searchPost } from 'utils/searchPost';

const useStyles = makeStyles<Theme>(({ spacing, breakpoints }) => ({
  textFieldWrapper: {
    paddingTop: spacing(4),
    [breakpoints.up('lg')]: {
      paddingTop: spacing(8),
    },
  },
  textField: {
    width: '100%',
  },
  postsWrapper: {
    marginTop: spacing(4),
  },
}));

const handleClickPost = (url: string) => linkEvent(url, '    ');

const SearchPage = () => {
  const styles = useStyles();
  const posts = useAllPosts();
  const [keyword, setKeyword] = useState('');
  const handleChange = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    setKeyword(e.target.value);
    searchEvent(e.target.value, '   ');
  }, []);

  const searchedPosts = useMemo(() => {
    return searchPost(keyword, posts);
  }, [keyword, posts]);

  return (
    <Layout footer={false}>
      <SEO title="" />
      <Grid container alignItems="flex-start" justify="center" className={styles.textFieldWrapper}>
        <Grid item xs={12} md={8}>
          <TextField
            placeholder=" "
            variant="outlined"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Icon.Search />
                </InputAdornment>
              ),
            }}
            value={keyword}
            onChange={handleChange}
            className={styles.textField}
          />
        </Grid>
      </Grid>
      <Grid container justify="center" className={styles.postsWrapper}>
        <Grid item xs={12} md={6}>
          <PostList posts={searchedPosts} onClickPost={handleClickPost} />
        </Grid>
      </Grid>
    </Layout>
  );
};

export default SearchPage;
